import { mapActions } from 'vuex';
import query from 'dom-helpers/query';
import ClickOutside from 'vue-click-outside';
import Avatar from '@/components/Components/Avatar/index.vue';
import Checkbox from '@/components/Components/Checkbox/index.vue';
import FileLoader from '@/components/Components/FileLoader/index.vue';
import FileUploader from '@/components/Components/FileUploader/index.vue';
import Location from './Location/index.vue';
import UrlPreview from './UrlPreview/index.vue';
import lib from '@/lib';

export default {
  name: 'RoomChatOutputMessage',
  props: ['message', 'isMobile'],
  data() {
    return {
      isShowActionsList: false
    };
  },
  components: {
    Avatar,
    Checkbox,
    FileLoader,
    FileUploader,
    Location,
    UrlPreview
  },
  directives: {
    ClickOutside
  },
  computed: {
    messageClass() {
      let classList = [this.senderType];
      if (this.$store.state.forward.selectMessages) {
        classList.push('forwardMode');
      }
      if (this.message.timespan) {
        classList.push('hasTimespan');
      }
      return classList;
    },
    senderType() {
      let systemTypes = lib.message.systemTypes;
      if (
        !this.message.sender ||
        ~systemTypes.indexOf(this.message.messageType)
      ) {
        return 'system';
      } else if (this.$store.state.client.client !== this.message.sender.id) {
        return 'others';
      } else {
        return 'self';
      }
    },
    urlExtras() {
      let urls = JSON.parse(this.message.originalUrl);
      let extras = JSON.parse(this.message.extra);
      extras = extras.map((extra, idx) => {
        if (extra.link) {
          return extra;
        } else {
          return {
            ...extra,
            link: urls[idx]
          };
        }
      });
      return extras;
    },
    readedCount() {
      const len = this.$store.state.roomActive.memberProperties.filter(
        member => {
          return (
            member.lastRead >= this.message.id &&
            member.client !== this.$store.state.client.client
          );
        }
      ).length;

      if (len === 1 && this.$store.state.roomActive.members.length === 2) {
        return '';
      } else {
        return len;
      }
    },
    onlyEmoji() {
      let onlyEmojiRegex = /^([\uD800-\uDBFF][\uDC00-\uDFFF])$/;
      return onlyEmojiRegex.test(this.message.message);
    },
    actions() {
      let actions = [];
      if (
        !this.message.status &&
        this.$store.state.config.chat.reply &&
        ~['text', 'url', 'image', 'video'].indexOf(this.message.messageType)
      ) {
        actions.push({
          key: 'reply',
          text: this.$i18n.t('Reply')
        });
      }
      if (
        !this.message.status &&
        this.$store.state.config.chat.forward &&
        ~['text', 'url', 'image', 'video', 'audio', 'file'].indexOf(
          this.message.messageType
        )
      ) {
        actions.push({
          key: 'forward',
          text: this.$i18n.t('Forward')
        });
      }
      return actions;
    },
    forwardChecked() {
      return !!this.$store.state.forward.messages.find(message => {
        return message.id === this.message.id;
      });
    }
  },
  mounted() {},
  methods: {
    addMembersMessage(message) {
      return lib.message.addMembersMessage(message);
    },
    textView(message) {
      return lib.message.textView(message);
    },
    onClick(e) {
      switch (this.message.messageType) {
        case 'image':
        case 'video':
          if (!query.closest(e.target, '.actions')) {
            this.$emit('showSilder');
          }
          break;
      }
    },
    gotoMessage(messageId, e) {
      if (e.target.tagName.toLowerCase() === 'a') {
        return;
      }
      this.$emit('gotoMessage', messageId);
    },
    findAncestor(element, className) {
      do {
        if (this.hasClass(element, className)) {
          return true;
        }
        element = element.parentElement;
      } while (element);
      return false;
    },
    hasClass(element, className) {
      if (!element.className) {
        return 0;
      }
      return ~element.className.split(/\s+/g).indexOf(className);
    },
    hideActionsList() {
      this.isShowActionsList = false;
    },
    doAction(key) {
      switch (key) {
        case 'reply':
          this.$emit('setReply');
          break;
        case 'forward':
          this.$store.state.forward.selectMessages = true;
          this.$store.state.forward.messages = [this.message];
          this.$emit('setForward');
          break;
        default:
          break;
      }
      this.isShowActionsList = false;
    },
    clickForwardCheckbox() {
      let idx = this.$store.state.forward.messages.findIndex(message => {
        return message.id === this.message.id;
      });
      if (idx < 0) {
        if (this.$store.state.forward.messages.length < 10) {
          this.$store.state.forward.messages.push(this.message);
        }
      } else {
        this.$store.state.forward.messages.splice(idx, 1);
      }
    },
    ...mapActions(['resendMessage'])
  }
};



